<template>
	<div class="headmenu">
		<!-- 收缩符号 -->
		<shrink  :toggleClick='toggleClick' :isActive='sidebar'></shrink>
		<!-- 面包屑 --><!-- 只是复用 -->
		<breadcrumb></breadcrumb>
		<!-- 用户信息 -->
      <el-dropdown class="avatar-container" trigger="click">
      <div class="avatar-wrapper">
        <img class="user-avatar" :src="avatar+'?imageView2/1/w/80/h/80'">
        <!-- <i class="el-icon-caret-bottom"></i> -->
      </div>
      <el-dropdown-menu class="user-dropdown" slot="dropdown">
        <router-link class="inlineBlock" to="/">
          <el-dropdown-item>
            Home
          </el-dropdown-item>
        </router-link>
        <el-dropdown-item divided>
          <span @click="logout" style="display:block;">LogOut</span>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
	</div>
</template>
<script>
	import shrink from '@/components/hamburger/shrink.vue'
	import breadcrumb from '@/components/breadcrumb.vue'
  import {mapGetters} from 'vuex'
	export default{
		components:{
			shrink,breadcrumb
		},
    computed:{
       // isActive:()=>this.$store.app.getters.sidebar
       ...mapGetters([
          'sidebar','avatar'
       ] )
    },
    created(){
      // console.log(this.$store.getters.avatar,'没有吗')
    },
		methods:{
      toggleClick(){
        console.log('我被点击了？')
        this.$store.commit('CHANGESIDEBAR')
      },
      logout(){
        this.$store.dispatch('LogOut').then(()=>{
          location.reload()//为了重新实例化vue-router对象
        })
      }
    }
	}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.headmenu{
		height: 40px;
		background: #eaeaea;
	}
  .navbar {
    height: 50px;
    line-height: 50px;
    border-radius: 0px !important;
   }
  .screenfull {
    position: absolute;
    right: 90px;
    top: 16px;
    color: red;
  }
   .hamburger-container {
    line-height: 58px;
    height: 50px;
    float: left;
    padding: 0 10px;
  }
   .avatar-wrapper{
      cursor: pointer;
      position: relative;
    }
    .user-avatar {
      width: 40px;
      height: 40px;
      border-radius: 20px;
    }
    .el-icon-caret-bottom {
      position: absolute;
      right: 14px;
      top: 25px;
      font-size: 12px;
    }
   .avatar-container{
    height: 50px;
    display: inline-block;
    position: absolute;
    right: 35px;
    }

</style>


