<template>
	<el-row class="tac">
  	<el-col :span="24">
        <el-menu  default-active="1" class="el-menu-vertical-demo" background-color="#545c64"
         text-color="#fff" active-text-color="#ffd04b"  :collapse="sidebar">
      <!-- 不会渲染name为Dashboard的路由 -->
      <template v-for="item in routes" v-if="!item.hidden&&item.children">
       <!-- 单个的导航，如果此路由对象children只有一个子元素， item.children.length===1-->
      <router-link v-if="item.children.length===1 && !item.children[0].children && !item.alwaysShow" :to="item.path+'/'+item.children[0].path"
        :key="item.children[0].name">
        <el-menu-item :index="item.path+'/'+item.children[0].path">
          <svg-icon v-if="item.children[0].meta&&item.children[0].meta.icon" :icon-class="item.children[0].meta.icon"></svg-icon>
          <!-- 如果meta且title存在 -->
          <!-- element自带的图标库比较少，所以使用icon-font -->
          <!-- <i class="el-icon-location"></i> -->
          <span v-if="item.children[0].meta&&item.children[0].meta.title" slot="title">{{item.children[0].meta.title}}</span>
        </el-menu-item>
      </router-link>
      <!-- 有子菜单的导航 -->
      <el-submenu v-else :index="item.name||item.path" :key="item.name">
        <template slot="title">
          <svg-icon v-if="item.meta&&item.meta.icon" :icon-class="item.meta.icon"></svg-icon>
          <span v-if="item.meta&&item.meta.title" slot="title">{{item.meta.title}}</span>
        </template>
        <!-- 遍历子菜单的children -->
        <template v-for="child in item.children" v-if="!child.hidden">
          <sidebar-item class="nest-menu" v-if="child.children&&child.children.length>0" :routes="[child]" :key="child.path"></sidebar-item>
          <router-link v-else :to="item.path+'/'+child.path" :key="child.name">
            <el-menu-item :index="item.path+'/'+child.path">
              <svg-icon v-if="child.meta&&child.meta.icon" :icon-class="child.meta.icon"></svg-icon>
              <span v-if="child.meta&&child.meta.title" slot="title">{{child.meta.title}}</span>
            </el-menu-item>
          </router-link>
        </template>
      </el-submenu>
    </template>
    </el-menu>
  </el-col>
</el-row>
</template>
<script>
import {mapGetters} from 'vuex'
	export default {
		props:{  //这种写法是限定传入变量类型
			routes:{
				type:Array
			}
		},
    computed:{
      ...mapGetters([
          'sidebar'
       ] )
    },
	  methods: {
	      handleOpen(key, keyPath) {
	        console.log(key, keyPath);
	      },
	      handleClose(key, keyPath) {
	        console.log(key, keyPath);
	      }
	    }
 	 }

</script>
<style>
 .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 250px;
  }
</style>


