<template>
	<el-aside width='auto'>
    <subitem :routes='routes'></subitem>
</el-aside>
</template>
<script>
    import subitem from './subitem.vue'
    export default{
      name:'sidebar',
      components:{subitem},
      data(){
        return {
         routes:this.$router.options.routes
        }
      }
    }
</script>
<style>
  .el-aside{
    height:1000px;
    background:rgb(84, 92, 100);
  }
</style>


