import axios from 'axios'
import {getToken} from '@/utils/auth.js'
import store from '../store/index.js'
import {Message,MessageBox} from 'element-ui'
//创建axios实例
/*创建实例
可以使用自定义配置新建一个 axios 实例
axios.create([config])
const instance = axios.create({
  baseURL: 'https://some-domain.com/api/',
  timeout: 1000,
  headers: {'X-Custom-Header': 'foobar'}
});
实例方法
以下是可用的实例方法。指定的配置将与实例的配置合并
axios#request(config)
axios#get(url[, config])
axios#delete(url[, config])
axios#head(url[, config])
axios#post(url[, data[, config]])
axios#put(url[, data[, config]])
axios#patch(url[, data[, config]])
*/
/*
可以自定义拦截器，在在请求或响应被 then 或 catch 处理前拦截它们。
 */
const service=axios.create({
	baseURL:process.env.BASE_API,//API的base_url
	timeout:15000//请求超时时间
})
//request拦截器
service.interceptors.request.use(config=>{
	//在发送请求前做些什么
	if(store.getters.token){  //判断是否有token
		config.headers['X-Token']=getToken()//让每个请求携带自定义token，可根据情况修改
	}
	return config
},
error=>{
	//Do something width request error
	console.log(error) //for debug
	Promise.reject(error)
})
//response拦截器,为axios实例添加拦截器
service.interceptors.response.use(
	response=>{
		/*
		code为非2000是抛错，可结合自己业务进行修改
		 */
	   const res=response.data
	   if(res.code!==20000){
	   	Message({
	   		message:res.message,
	   		type:'error',
	   		duration:5*1000
	   	})
	   	//50008：非法的token,50012:其他客户端登录了;50014:Token过期了
	   	if(res.code===50008||res.code===50012||res.code===50014){
	   		MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录','确定登出',{
	   			confirmButtonText:'重新登录',
	   			cancelButtonText:'取消',
	   			type:'warning'
	   		}).then(()=>{
	   			store.dispatch('FedLogOut').then(()=>{
	   				location.reload()//为了重新实例化vue-router对象，避免bug
	   			})
	   		})
	   	}
	   	return Promise.reject('error')
	   }else{
	   	return response.data
	   }
	},
	error=>{
		console.log('err'+error)//for debug
		Message({
			message:error.message,
			type:'error',
			duration:5*1000
		})
		return Promise.reject(error)
	}
	)
export default service


