export default {
	state:{
		sidebar:false
	},
	mutations:{
		CHANGESIDEBAR:state=>state.sidebar=!state.sidebar   //是否收缩侧边栏
	},
	getters:{
	sidebar:state=>state.sidebar   //模块里的getters不会区分模块，会直接到store.getters里，所以不能重名，如果getters写在模块里，也
	// 不要写成state.app.sidebar,不需要带模块名了
	}
}
