import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/views/layout/index.vue'
import login from '@/views/login.vue'
Vue.use(Router)

export default new Router({
  routes: [   //routes是一个数组，组件中会有type:array的限制
    {
      path:'/login',
      component:login,
      name:'login'
    },
    {
      path: '/',
      component: layout,
      name: 'Dashboard',  //一个默认路由，侧边栏遍历路由时，判断后不会遍历这个路由记录
      redirect:'/dashboard',
      hidden: true,  //一个flag
      children:[{
        path:'dashboard',
        component:()=>import('@/views/dashboard.vue')
      }]
    },
    {
      path:'/example',
      name:'Example',
      component:layout,
      redirect:'/example/table',  //默认路由
      meta: { title: '综合实例', icon: 'example' },  //根据路由来动态生成侧边栏
      children:[  //v-else
        {
          path:'table',  //没有写/table，在组件中加入手动拼接了+'/'+
          name:'Table',
          component:()=>import('@/views/table.vue'),
          meta:{title:'表格',icon:'table'}
        },
        {
          path:'tree',
          name:'Tree',
          component:()=>import('@/views/tree.vue'),
          meta:{title:'树',icon:'tree'}
        }
      ]
    },
    {
      path:'/form',
      component:layout,
      children:[{   //children只有一个子元素，v-if
        path:'index',
        name:'From',
        component:()=>import('@/views/form.vue'),
        meta:{title:'表单',icon:'form'}
      }]
    },
    {
      path:'/404',
      component:()=>import('@/views/404.vue'),
      hidden:true
    },
    {
      path:'*',
      redirect:'/404',
      hidden:true
    }
  ]
})



