
import axios from '@/utils/request'
export const login=(username, password)=>{
 return axios.post('/user/login',{username,password})
}
export const getInfo=(token)=>{
  return axios.get( '/user/info',{params:{token:token}} )
}
export const logout=()=>{
  return axios.post('/user/logout')
}
/*
执行 GET 请求
// 为给定 ID 的 user 创建请求
// GET 参数可以放到params里（推荐）
axios.get('/user', {
    params: {
      ID: 12345
    }
  })
  .then(function (response) {
    console.log(response);
  })
  .catch(function (error) {
    console.log(error);
  });

// 还可以使用ECMAScript 2017里的async/await，添加 `async` keyword to your outer function/method.
async function getUser() {
  try {
    const response = await axios.get('/user?ID=12345');
    console.log(response);
  } catch (error) {
    console.error(error);
  }
}
 async/await 是 ECMAScript 2017新提供的功能 ，Internet Explorer 和一些旧的浏览器并不支持

 */


